import cmdProgress from '../cmd-progress/cmd-progress.vue';
export default {
	components: {
		cmdProgress
	},
	data() {
		return {
			visible: false,
			progress: 0,
			contents: [],
			downloading: false,
			success: true
		};
	},
	props: {
		type: {
			type: String,
			default: 'pkg'
		},
		url: {
			type: String,
			default: ''
		},
		title: {
			type: String,
			default: '版本更新'
		},
		content: {
			type: String,
			default: ''
		}
	},
	watch: {
		content() {
			var strs = this.content.split('\\n');
			for (var i = 0; i < strs.length; i++) {
				this.contents.push(strs[i]);
			}
		}
	},
	methods: {
		show() {
			setTimeout(() => {
				if (this.success) {
					this.visible = true;
				}
			}, 100);
		},
		hide() {
			setTimeout(() => {
				this.visible = false;
			}, 100);
		},
		onCancelClick() {
			this.hide();
		},
		onSureClick() {
			this.downloading = true;
			// this.progress = 10;
			var platform = uni.getSystemInfoSync().platform;
			if (platform == 'ios' && this.type == 'pkg') {
				plus.runtime.openURL(this.url);
			} else {
				this.downloading = true;
				var downloadTask = uni.downloadFile({
					url: this.url,
					success: downloadResult => {
						this.hide();
						if (downloadResult.statusCode === 200) {
							plus.runtime.install(
								downloadResult.tempFilePath,
								{
									force: false
								},
								function() {
									plus.runtime.restart();
								},
								e => {
									console.log(e);
									this.success = false;
									uni.showToast({
										title: '安装升级包失败',
										icon: 'none'
									});
								}
							);
						}
					}
				});
				downloadTask.onProgressUpdate(e => {
					this.progress = e.progress;
				});
			}
		}
	}
};
